import { fetchParams } from "./utils/fetch.config.js";

const defaultValues = {
  search: "react",
  page: 0,
  hitsPerPage: 20,
};

export default {
  data() {
    return {
      list: [],
      pending: true,
      error: false,
      ...defaultValues,
    };
  },
  methods: {
    async getData(searchValue) {
      this.pending = true;
      this.error = false;

      const response = await fetchParams({
        search: searchValue,
        page: this.page,
        hitsPerPage: this.hitsPerPage,
      });

      this.pending = false;

      if (!response.error) {
        this.list = response.hits;
      } else {
        this.error = true;
        // Show error
      }
    },
  },
  mounted() {
    this.getData(this.search);
  },
};
